import React, { Fragment } from 'react'
import {Grid, Cell, Card, CardTitle, CardText, CardActions} from 'react-mdl';
import {Link} from 'react-router-dom'


const About = () => {
    return (
        <div id='ab' style={{marginTop:"100px"}}>
             <Fragment >
            <div shadow={5}>
                <Grid className="demo-grid-3" style={{  
                        marginTop: '8em',
                        width: '80%',
                        height:'40%',
                        color: 'black',  
                        
                    }} >
                    <Cell col ={12}>
                        <h2 style={{fontFamily: 'Trebuchet MS', fontWeight:'lighter', lineHeight:'2em'}}><center>Who Am I?</center></h2>
                        <h4 style={{color: 'black'}}>
                            <center>
                            
                            I am a junior web developer. I do full stack web delopment, but I'm mostly passionate about the functionality of things, the backend. <br/>
                                
                                                    </center>
                        </h4>
                    </Cell>
                </Grid>
                <Grid className="container" style={{marginTop: '5em'}}>
                    <Cell  col={6}>
                        <Card shadow={5} style={{zIndex:0,width:'80%',height:'500px', margin: 'auto'}}>
                            <div style={{backgroundColor:'white', textAlign:'center', marginTop: '2em'}}>
                                <img src="https://i.pinimg.com/originals/5b/18/08/5b18084d03c0ba117c60a98a0d0d02b5.jpg" alt="Avatar" style={{width:'15%'}}/>
                            </div>
                            <div style={{color: 'grey', textAlign:'center', paddingBottom: '-10em'}}>
                                <h2>Education</h2>
                                <h4> Lifechoices Coding Academy</h4>
                                <div style={{fontFamily:'Tahoma'}}>
                                    Period: Feb, 2020 - Aug, 2020.<br/>
                                    Course: Fullstack Web Development<br/>
                                    
                                </div>
                                <h4>Northlink College</h4>
                                <div style={{fontFamily:'Tahoma'}}>
                                    Period: 2016 - 2018.<br/>
                                    Course: Logic Systems, Electrical Engineering<br/>
                                    
                                </div>
                            
                            </div>
                        </Card>
                    </Cell>
                    <Cell  col={6}>
                        <Card shadow={5} style={{zIndex:0,width:'80%',height:'500px', margin: 'auto'}}>
                        <div style={{backgroundColor:'white', textAlign:'center', marginTop: '2em'}}>
                                <img src="https://i.pinimg.com/originals/5b/18/08/5b18084d03c0ba117c60a98a0d0d02b5.jpg" alt="Avatar" style={{width:'15%'}}/>
                            </div>
                            <div style={{color: 'grey', textAlign:'center', paddingBottom: '-12em'}}>
                                <h2>Experience</h2>
                                
                                <div style={{fontFamily:'Tahoma'}}>
                                <h4> LC Studio</h4>
                                    Period: Dec, 2020 - .<br/>
                                    Role: Intern<br/>
                                    Duties: Junior web developer<br/>
                                </div>
                                <div style={{fontFamily:'Tahoma'}}>
                                <h4>Sage Futuremakers Foundation</h4>
                                    Period: Sep - Nov, 2019. Feb, 2020.<br/>
                                    Role: Volunteer facilitator<br/>
                                    Duties: <br/>
                                    Teach basic programming skills to high school pupils, 
                                    Recruite potential students around townships.
                                </div>
                            </div>
                        </Card>
                    </Cell>
                </Grid>
            </div>
        </Fragment>
        </div>
       
    )
}

export default About;